(function() {
  if(Meteor.isCordova){
    console.log("run in app of mobile.")
  }else{
    console.log("run in web browser.")
    var Agent=function(){
      var self=this;
      var callParent=function(method, args) {
        console.log("call parent");
        var data;
        if (args == null) {
          args = [];
        }
        data = {
          src: 'rocketchat',
          fn: method,
          args: args
        };
        return window.parent.postMessage(JSON.stringify(data), '*');
      };
      self.toggleWindow=function(){
        //åˆ‡æ¢çª—å£
        callParent("toggleWindow")
      }
      self.noticeNewMsg=function(msg){
        callParent("noticeNewMsg",msg);
      }
      self.receiveAWSUrl=function(data){
        callParent("receiveAWSUrl",{url:data.url,id:data.id});
      }
      self.imagePreview=function(data){
        callParent("imagePreview",{id:data.id,list:data.list});
      }
      self.closeWindow=function(){
        callParent("closeWindow");
      }
      self.signACK=function(msg){
        //no need to send ack back to outer web browser
        console.log(msg)
      }
      var message= null;
      self.cacheUniqueSystemMessage=function(msg){
        message = msg;
      }
      self.recoverUniqueSystemMessage=function(){
        var result = message;
        message = null;
        return result;
      }
      var userInfo= null;
      self.cacheUniqueUserInfo=function(msg){
        userInfo = msg;
      }
      self.recoverUniqueUserInfo=function(){
        var result = userInfo;
        userInfo = null;
        return result;
      }
      if(window.addEventListener){
        window.addEventListener('message',function(msg){
          receiveMessage(msg);
        })
      }else if (window.attachEvent){
        window.attachEvent('onmessage', function (msg) {
          receiveMessage(msg);
        })
      }
      var receiveMessage=function(msg){
        if(msg.data){
          try{
            var msgData =  eval("("+msg.data+")");
            if (typeof msgData === 'object' && msgData.src !== undefined && msgData.src === 'livechat-parent') {
              console.log('receiveMessage->',msg);
              var args = [].concat(msgData.args || []);
              return api[msgData.fn].apply(null, args);
            }
          }catch(e){
            //msg.data is not a json string
          }
        }
      }
      var api={
        transferToAWSUrl:function(message){
          if(window[message.url]){
            callParent('receiveAWSUrl',{url:window[message.url],id:message.id});
          }else{
            Meteor.call('awsGetSignedUrl', message.url, function (error, awsUrl) {
              if (awsUrl !== null && awsUrl !== undefined){
                callParent('receiveAWSUrl',{url:awsUrl,id:message.id});
              }else{
                callParent('receiveAWSUrl',{url:null,id:message.id});
              }
            })
          }
        }
      }
    }
    this.outerAgent = new Agent();
  }
}).call(this);
